<template>
  <div id="chartBox">

  </div>

</template>
<script>
//  ECharts 
var echarts = require('echarts/lib/echarts');
require('echarts/lib/chart/line');
require('echarts/lib/component/tooltip');
require('echarts/lib/component/legend');
require('echarts/lib/component/visualMap');
require('echarts/lib/component/markLine');
import {
  mapGetters,
  mapMutations,
  mapActions
} from 'vuex'
export default {
  props: {
    weights: Array
  },
  mounted () {
    if (this.weights.length) {
      this.draw()
    }
  },
  watch: {
    weights: {
      handler (curVal, oldVal) {
        if (curVal.length) {
          this.draw()
        }
      },
      deep: true
    },
    userInfo: {
      handler () {
        this.draw()
      },
      deep: true
    }
  },
  computed: {
    weightall () {
      let temp = this.weights.map(item => {
        return item.AM * 1 > item.PM * 1 ? item.AM * 1 : item.PM * 1
      })
      temp.push(this.idealWeight, this.normalWeight)
      return temp.sort((a, b) => a - b)
    },
    min () {
      return this.weightall[0] * 1
    },
    max () {
      return this.weightall[this.weightall.length - 1] * 1
    },
    idealWeight () {
      return this.userInfo.idealWeight ? this.userInfo.idealWeight : 0
    },
    height () {
      return this.userInfo.height ? this.userInfo.height : 0
    },
    normalWeight () {
      return parseInt(22 * this.height * this.height * 0.0001)
    },
    standerdmin () {
      return this.normalWeight > this.idealWeight ? this.idealWeight : this.normalWeight
    },
    standerdmax () {
      return this.normalWeight > this.idealWeight ? this.normalWeight : this.idealWeight
    },
    ...mapGetters(['userInfo'])
  },
  methods: {
    draw () {
      // 
      // 
      // domecharts
      let myChart = echarts.init(document.getElementById('chartBox'))
      // 
      myChart.setOption({
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          right: 0,
          top: 0,
          orient: 'vertical',
          tooltip: {
            show: true
          }
        },
        xAxis: {
          data: this.weights.map(item => item.date)
        },
        yAxis: {
          splitLine: {
            show: false
          },
          min: this.min,
          max: this.max
        },
        visualMap: {
          show: this.idealWeight > 0 ? true : false,
          top: 0,
          left: 0,
          orient: 'horizontal',
          pieces: this.idealWeight ? [{
            gt: 0,
            lte: this.standerdmin,
            color: 'aqua'
          }, {
            gt: this.standerdmin,
            lte: this.standerdmax,
            color: '#ffde33'
          }, {
            gt: this.standerdmax,
            color: '#f56c6c'
          }] : []
        },
        series: [
          {
            name: '',
            type: 'line',
            smooth: true,
            connectNulls: true,
            data: this.weights.map(function (item) {
              return item.AM
            }),
            markLine: {
              silent: true,
              data: [
                {
                  yAxis: this.idealWeight,
                  label: {
                    position: 'middle',
                    formatter: ''
                  },
                  lineStyle: {
                    color: this.standerdmin == this.idealWeight ? 'aqua' : '#ffde33'
                  }
                },
                {
                  yAxis: this.normalWeight,
                  label: {
                    position: 'middle',
                    formatter: ''
                  },
                  lineStyle: {
                    color: this.standerdmin != this.idealWeight ? 'aqua' : '#ffde33'
                  }
                }
              ]
            }
          },
          {
            name: '',
            type: 'line',
            smooth: true,
            connectNulls: true,
            data: this.weights.map(function (item) {
              return item.PM
            }),
            markLine: {
              silent: true,
              data: [
                {
                  yAxis: this.idealWeight,
                  label: {
                    position: 'middle',
                    formatter: ''
                  },
                  lineStyle: {
                    color: this.standerdmin == this.idealWeight ? 'aqua' : '#ffde33'
                  }
                },
                {
                  yAxis: this.normalWeight,
                  label: {
                    position: 'middle',
                    formatter: ''
                  },
                  lineStyle: {
                    color: this.standerdmin != this.idealWeight ? 'aqua' : '#ffde33'
                  }
                }
              ]
            }
          }
        ],

      })
    }
  }
}
</script>

<style lang="scss" scoped>
#chartBox {
    width: 100%;
    height: 750px;
}
</style>



